import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faChevronLeft, faChevronRight } from "@fortawesome/free-solid-svg-icons"
import { useSwipeable } from "react-swipeable"

import "./Carousel.css";

export const CarouselItem = ({ children, width }) => {
  return (
    <div className="carousel-item" style={{ width: width }}>
      {children}
    </div>
  )
}

const Carousel = ({ children }) => {
  const [activeIndex, setActiveIndex] = React.useState(0);
  const [displayIndicators, setDisplayIndicators] = React.useState(false);

  const updateIndex = (newIndex) => {
    if (newIndex < 0) {
      newIndex = React.Children.count(children) - 1;
    } else if (newIndex >= React.Children.count(children)) {
      newIndex = 0;
    }

    setActiveIndex(newIndex);
  }

  const handlers = useSwipeable({
    onSwipedLeft: () => updateIndex(activeIndex + 1),
    onSwipedRight: () => updateIndex(activeIndex - 1)
  });

  return (
    <div
         {...handlers}
         className="carousel"
         role="button"
         tabIndex={0}
         onMouseOver={() => setDisplayIndicators(true)}
         onFocus={ () => void 0 }
         onMouseLeave={() => setDisplayIndicators(false)}
    >
      <div className="inner" style={{ transform: `translateX(-${activeIndex * 100}%)` }}>
        {React.Children.map(children, (child) => {
          return React.cloneElement(child, { width: "100%" });
        })}
      </div>
      <div style={{ display: displayIndicators ? "block" : "none" }}>
        <button className="indicators indicators-left" onClick={() => {
          updateIndex(activeIndex - 1)
        }}>
          <FontAwesomeIcon icon={faChevronLeft} />
        </button>
        <button className="indicators indicators-right" onClick={() => {
          updateIndex(activeIndex + 1)
        }}>
          <FontAwesomeIcon icon={faChevronRight} />
        </button>
      </div>
      {React.Children.map(children, (child, index) => (
        <span className={`pageIndicators ${index === activeIndex ? "highlightIndicator" : ""}`} />
      ))}
    </div>
  )
}

export default Carousel;